import netmagLogo from "components/Nav/Logos/netmag.svg";

const StyledNav = styled.div`
  background-color: #222222;
  overflow-x: scroll;
  overflow-y: hidden;
  height: 180px;
  -webkit-overflow-scrolling: touch;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  @media (min-width: 768px) {
    height: 300px;
  }
`;

const Figure = styled.figure`
 text-align: center;
 margin: 0;
`;

const Img = styled.img`
 width: 100%;
 max-height: 100%;
 margin: 0;
`;

const Figcaption = styled.figcaption`
 height: 0px;
 overflow: hidden;
`;

const Ul = styled.ul`
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
`;

const Li = styled.li`
  min-width: 150px;
  display: block;
  margin: 0;
  padding: 0;
`;

const StyledLink = styled(Link)`
  display: block;
  color: #cccccc;
  margin: 0;
  padding: 1em;
  opacity: 0.7;
  transition: all .2s ease-in-out;
  filter: grayscale(1);
  backface-visibility: hidden;
  box-sixing: border-box;
  transform: scale(0.9);
  will-change: transform;
  &:hover, &:focus {
    filter: grayscale(0);
    opacity: 1;
    transform: scale(1);
  }
`;

